
.border_top{border-top: 1px dotted;}

.border_right{border-right: 1px dotted;}

.border_bottom{border-bottom: 1px dotted;}

.border_left{border-left: 1px dotted;}




#mail_container{
  width: 760px;
  font-family: sans-serif !important;
  background-color: #FFFFFF;
}

#mail_top_bar{
  height: 25px;
  text-align: center;
  font-size: 11px;
}

#mail_banner{
  height: 100px;
}

#mail_separator{
  height: 20px;
}

#mail_body{
  width: 580px;
  padding: 0px 20px 30px 15px;
  font-size: 12px;
}

#mail_disclaimer{
  font-size: 10px;
  margin-top: 60px;
  text-align: justify;
}

#mail_ticket{
  width: 180px;
  padding-right: 15px;
  padding-left: 15px;
  font-size: 12px;
  padding-bottom: 30px;
}

.manifest_ticket_key {
  text-transform: uppercase;
  font-size: 8px;
  font-weight: bold;
}

.manifest_ticket_value {
  margin-bottom: 8px;
}

#mail_ribbon{
  height: 80px;
  width: 760px;
  font-size: 18px;
  text-align: center;
}

#mail_ribbon_bib, #ribbon_size{
  width: 30%;
}

#mail_ribbon_barcode{
  width: 40%;
}

#mail_footer{
  height: 100px;
  font-size: 10px;
  padding: 30px 50px 40px 50px;
  text-align: justify;
}

#mail_copyright{
  height: 30px;
  text-align: right;
  padding: 15px;
  font-size: 10px;
}
